.App {
	text-align: center;
	margin: 0;
	padding: 0;
	font-family: sans-serif;
}

.container {
	width: 100%;
	display: table;
}
.IO.container { height: 90vh; }
.row {
	display: table-row;
}
.column {
	display: table-cell;
}
.column legend {
	height: 6vh;
	position: relative;
}
.column .title {
	font-weight: bold;
	position: absolute;
	bottom: 0;
}

.column textarea {
	width: 95%;
	height: 80vh;
}
textarea.error {
	background-color: #fcc;
}

.map-container {
	width: 100%;
	height: 400px;
}
.mapmarker:before {
	content: "";
	position: absolute;
	top: 0;
	left: 0;
	-webkit-transform: translate(-2px, -7px) rotate(45deg);
	    -ms-transform: translate(-2px, -7px) rotate(45deg);
	        transform: translate(-2px, -7px) rotate(45deg);
	height: 0;
	width: 0;
	border-top: 10px solid transparent;
	border-bottom: 10px solid transparent;
	border-right: 10px solid #555;
}
.mapmarker {
	border-radius: 10px;
	position: relative;
	height: 24pt;
	width: 32pt;
	padding: 4pt;
}
.mapmarker:hover {
	z-index: 99;
}
.office {
	color: white;
	background-color: #1f8ded;
	border: 1px solid white;
}
.customer {
	background-color: white;
	border: 1px dotted grey;
	
}
.close {
	background-color: #cfc;
}

.threshold {
	margin: 4pt;
	margin-right: 0;
	padding: 4pt;
	font-size: 16pt;
	font-weight: bold;
	height: 18pt;
	width: 48pt;
}

button {
	margin: 4pt;
	padding: 4pt;
	border: 1px solid grey;
	border-radius: 5px;
	color: white;
	background-color: #1f8ded;
}



